.container{height:100vh;display:flex;justify-content:center;align-items:center;background:#f7f7f7;flex-direction:column}.logo{min-width:80px;max-width:120px;width:100%;margin-bottom:50px}.login{max-width:480px;height:474px;padding:30px 80px;display:flex;flex-direction:column;align-items:center;justify-content:space-between;margin:20px;background:#fff;-webkit-box-shadow:0px 0px 36px 21px rgba(34,60,80,.15);-moz-box-shadow:0px 0px 36px 21px rgba(34,60,80,.15);box-shadow:0px 0px 36px 21px rgba(34,60,80,.15);border-radius:8px}.login-input-block input{width:100%;height:40px;background:#fff;border:1px solid #c8c6c5;border-radius:2px;padding:10px}.login-input-block span{color:red}.login-input-block span::before{display:inline;content:"⚠ "}.login-btn-block{display:flex;align-items:center;flex-wrap:wrap;justify-content:center}.login-btn-block button:last-child{margin-left:20px;background:#582eff}.login__link_block{display:flex;flex-direction:column}.login__link:last-child{margin-top:15px}@media screen and (max-width: 900px){.login-title{margin-bottom:0}.login-btn-block button{width:100%}.login-btn-block button:last-child{margin:10px 0 10px 0}}